@import "variables";

body{
  .header__fixedNav {
    display: none;
    width: 86%;
    position: fixed;
    top: 0;
    left: 50%;
    margin-left: -43%;
    z-index: 15;
    height: 50px;
    background-color: @mainColor;
    ul {
      display: flex;
      background-color: @fontColor;
      li {
        &:nth-child(1) {
          width: 20%;
          background-color: #77c9cc;
          a {
            display: flex;
            justify-content: center;
            align-items: center;
            font: 1.125em/50px @sansbold;
            color: @linkColor;
            text-decoration: none;
            i {
              padding: 0 15px 0 20px;
              font-size: 1.5em;
              line-height: 50px;
            }
            &:hover {
              color: #fff;
              background-color: @hoverColor;
              transition: .5s;
            }
          }
        }
        &:nth-child(2) {
          width: 55%;
          display: flex;
          justify-content: center;
          align-items: center;
          input {
            width: 90%;
            height: 35px;
            margin-left: 15px;
            padding-left: 25px;
            font: 1em/50px @sansregular;
            color: #9e9e9e;
            border: 3px solid @fontColor;
            border-radius: 50px;
            outline: none;
          }
          i {
            font-size: 1.2em;
            line-height: 45px;
            position: relative;
            left: -50px;
            color: @mainColor;
          }
        }
        &:nth-child(3) {
          width: 17%;
          a {
            display: flex;
            justify-content: center;
            align-items: center;
            font: 1.125em/50px @sansregular;
            color: #fff;
            text-decoration: none;
            i {
              padding: 5px;
              font-size: 1.5em;
            }
          }
        }
        &:nth-child(4) {
          width: 3%;
          a {
            display: inline-block;
            text-decoration: none;
            i {
              font-size: 3em;
              color: #7d3daf;
              line-height: 50px;
              background-color: #fff;
            }
          }
        }
        &:nth-child(5) {
          width: 15%;
          a {
            display: flex;
            justify-content: center;
            align-items: center;
            font: 1.125em/50px @sansregular;
            color: #fff;
            text-decoration: none;
            i{
              font-size: 1.5em;
            }
          }
        }
      }
    }
  }
}